.popup__button {
    margin-top: 43px;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #ffffff;
    background-color: #000000;
    outline: none;
    border: none;
    width: 100%;
    height: 50px;
    border-radius: 2px;
    transition: background-color .3s linear;
    opacity: 1;
}

.popup__button:hover {
    background-color: rgba(0, 0, 0, 0.8);
    cursor: pointer;
}

@media (max-width: 425px) {
    .popup__button {
        font-size: 14px;
        line-height: 17px;
        height: 40px;
        margin-top: 36px;
    }
}
