.popup__close {
    position: absolute;
    top: -37px;
    right: -35px;
    width: 30px;
    height: 40px;
    opacity: 1;
    z-index: 2;
    transition: opacity .3s linear;
    background-color: transparent;
    outline: none;
    border: none;
    padding: 0;
}

.popup__close::before {
    content: '';
    position: absolute;
    left: 20px;
    top: 0;
    height: 40px;
    width: 4px;
    transform: rotate(45deg);
    background-color: #ffffff;
}

.popup__close::after {
    content: '';
    position: absolute;
    left: 20px;
    top: 0;
    height: 40px;
    width: 4px;
    transform: rotate(-45deg);
    background-color: #ffffff;
}

.popup__close:hover {
    opacity: 0.6;
    cursor: pointer;
}

@media (max-width: 425px) {
    .popup__close {
        height: 30px;
        top: -25px;
        right: -20px;
    }

    .popup__close::before {
        height: 30px;
    }

    .popup__close::after {
        height: 30px;
    }
}
