.header {
    max-width: 882px;
    width: 100%;
    margin: 0 auto;
    padding-top: 45px;
    min-height: 119px;
    border-bottom: 1px solid #545454;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
}

@media (max-width: 882px) {
    .header {
        padding-left: 27px;
        padding-right: 30px;
        padding-top: 28px;
        min-height: 84px;
    }
}
