.profile__name {
    margin: 0;
    font-size: 42px;
    line-height: 1.33;
    font-style: normal;
    font-weight: 500;
    padding-right: 17px;
    box-sizing: border-box;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media (max-width: 882px) {
    .profile__name {
        text-align: center;
        margin-top: 15px;
        font-size: 27px;
        line-height: 2.07;
    }
}
