.popup__input {
    height: 40px;
    width: 100%;
    border: none;
    border-bottom: 1px solid rgb(0, 0, 0, .2);
    font-family: inherit;
    font-size: 14px;
    line-height: 17px;
    padding: 0;
}

.popup__input:focus {
    outline: none;
}

@media (max-width: 425px) {
    .popup__input {
        font-size: 12px;
        line-height: 15px;
    }
}
