.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 40px;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s, opacity .3s ease;
}
