.popup__form {
    position: relative;
    max-width: 430px;
    width: 100%;
    background-color: #ffffff;
    color: #000000;
    padding: 36px;
    box-sizing: border-box;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media (max-width: 505px) {
    .popup__form {
        width: 100%;
        min-height: 258px;
    }
}
